}

.Header-image {
  background-size: contain;
  width: 100%;
  height: auto;
  max-width: 450px;
  margin: 0 auto;
  animation: bounce 3s infinite ease-in-out;
  -webkit-animation: bounce 3s infinite ease-in-out;
  -o-animation: bounce 3s infinite ease-in-out;
  -ms-animation: bounce 3s infinite ease-in-out;
  -moz-animation: bounce 3s infinite ease-in-out;
}

.Header-title {
  color: #324168;
  font-family: Dosis;
  font-size: 32px;
  font-weight: 700;
  line-height: 42px;
  margin-bottom: 24px;
}

.Header-copy {
  color: #707f8f;
  font-size: 18px;
  line-height: 27px;
  font-family: Dosis;
  margin-bottom: 24px;
}

.Header-form {
  border-radius: 12px;
  box-shadow: 0px 12px 32px rgba(131, 155, 179, 0.16);
  overflow: hidden;
  margin-bottom: 24px;
}

.NoHassle {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
}

