.Footer {
  padding-bottom: 80px;
  padding-top: 80px;
}

.Footer-content {
  padding-bottom: 48px;
}

.Footer-list-title {
  font-size: 18px;
  font-family: Dosis;
  font-weight: 700;
  margin: 0 0 12px;
  text-transform: capitalize;
}

.Footer-list-title,
.Footer-title,
.Nav-link:hover {
  color: #162637;
}

.Footer-link {
  color: #707f8f;
  font-size: 16px;
  font-family: Dosis;
  line-height: 32px;
  transition: ease-in-out 200ms color;
}

.Footer-right {
  display: flex;
  flex-wrap: wrap;
}

.Footer-link:hover,
.Footer-title div,
.Nav--sticky .Nav-webApp-link--signIn:hover,
.Nav-mobileButton span,
.Nav-webApp-link--signIn:hover {
  color: rgb(94, 85, 173);
}

.Footer-item {
  color: #707f8f;
  font-size: 16px;
  font-weight: 500;
  line-height: 34px;
  transition: ease-in-out 200ms color;
}

.Footer-list-group {
  margin-bottom: 15px;
}

.Footer,
.Form,
.Nav,
.Tools-description {
  font-family: Dosis;
}

@media (min-width: 1000px) {
  .Footer-right {
    display: flex;
    flex-wrap: wrap;
  }
}

@media (min-width: 1000px) {
  .Footer-content {
    order: 1;
  }
}

@media (min-width: 1000px) {
  .Footer-right {
    display: flex;
    flex-wrap: wrap;
  }
}

@media (min-width: 1000px) {
  .Footer-row {
    display: flex;
    margin-bottom: 120px;
  }
  .Footer-list-group {
    min-width: 206px;
  }
}

@media (min-width: 1000px) {
  .Footer-list-title {
    font-size: 20px;
    margin: 0 0 21px;
  }
}
